.ninja {
  width: 200px;
  height: 200px;
  margin: auto;
  position: relative;
  background-color: #b5babf;
  border-radius: 50%;
  overflow: hidden;
  -webkit-transition: all .5s;
  transition: all .5s;
}
.ninja * {
    position: absolute;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}
.ninja.beforeInit {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    opacity: 0;
}
.ninja__loading {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 0 solid #a1a6ab;
  border-left-color: #1791c3;
}
.ninja__box {
  width: 100%;
  height: 100%;
  -webkit-perspective: 100px;
          perspective: 100px;
}
.ninja__head {
  background-color: #4e4848;
  top: 31px;
  left: 41px;
  height: 150px;
  width: 115px;
  border-radius: 50px 50px 45px 45px;
  -webkit-transform: rotateX(-7deg);
          transform: rotateX(-7deg);
  -webkit-box-shadow: inset 7px 0px #433b3a;
          box-shadow: inset 7px 0px #433b3a;
}
.ninja__bandHand {
  top: 56px;
  width: 100%;
}
.ninja__band {
  background: #1791c3;
  width: 126px;
  height: 20px;
  left: 35px;
  top: 0;
  border-radius: 2px;
  -webkit-transform: rotateX(-3deg);
          transform: rotateX(-3deg);
  z-index: 2;
}
.ninja__logo {
  background: #00c2ff;
  width: 36px;
  height: 34px;
  top: -8px;
  left: 46px;
  border-radius: 50%;
}
.ninja__logo * {
    width: 4px;
    height: 13px;
    background: #FFF;
    border-radius: 6px;
}
.ninja__detail--1 {
  top: 11px;
  left: 8px;
}
.ninja__detail--2 {
  top: 14px;
  left: 16px;
}
.ninja__detail--3 {
  top: 11px;
  left: 24px;
}
.ninja__visor {
  background: #ebc7a4;
  width: 88px;
  height: 33px;
  top: 94px;
  left: 55px;
  border-radius: 8px 8px 15px 15px;
  -webkit-box-shadow: inset 6px 0 #d0ad8b;
          box-shadow: inset 6px 0 #d0ad8b;
}
.ninja__visor > div {
    width: 25px;
    height: 9px;
    bottom: 14px;
    background: #FFF;
    overflow: hidden;
}
.ninja__iris {
  width: 1px;
  height: 1px;
  -webkit-box-shadow: 0 0 0 5px #000;
          box-shadow: 0 0 0 5px #000;
  background-color: #000;
  display: block;
  border-radius: 50%;
  position: absolute;
  bottom: 5px;
  left: 46%;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.ninja__eye--left {
  left: 10px;
  border-radius: 0 60%;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
}
.ninja__eye--right {
  right: 10px;
  border-radius: 60% 0;
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
}
.ninja__hand {
  top: 200px;
  width: 100%;
}
.ninja__hand > div {
    background: url(data:image/svg+xml;base64,) top center/contain no-repeat;
    width: 70px;
    height: 185px;
}
.ninja__hand--left {
    left: -16px;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.ninja__hand--right {
    right: -12px;
}
.ninja.loading .ninja__loading {
  -webkit-animation: spin .8s infinite linear;
          animation: spin .8s infinite linear;
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  border-width: 4px;
}
.ninja.look .ninja__head {
  -webkit-box-shadow: inset 7px 7px #433b3a;
          box-shadow: inset 7px 7px #433b3a;
  -webkit-transform: rotateX(-10deg);
          transform: rotateX(-10deg);
}
.ninja.look .ninja__bandHand {
  top: 70px;
}
.ninja.look .ninja__logo {
  top: -6px;
}
.ninja.look .ninja__visor {
  top: 105px;
  height: 30px;
}
.ninja.look .ninja__visor > div {
    bottom: 12px;
    height: 9px;
}
.ninja.look .ninja__iris {
  bottom: 2px;
}
.ninja.notLook .ninja__visor > div {
  height: 0px;
  border-top: 2px solid #a7825e;
}
.ninja.upHand .ninja__hand {
  top: 0;
}
.ninja.downBand .ninja__bandHand {
  top: 100px;
}
.ninja.closeEye .ninja__visor > div {
  height: 0px;
  border-top: 2px solid #a7825e;
}
@-webkit-keyframes loginInvalid__box {
0%, 100% {
    left: 0;
}
20%, 60% {
    left: -3%;
}
40%, 80% {
    left: 3%;
}
}
@keyframes loginInvalid__box {
0%, 100% {
    left: 0;
}
20%, 60% {
    left: -3%;
}
40%, 80% {
    left: 3%;
}
}
@-webkit-keyframes loginInvalid__logo {
0%, 100% {
    left: 45px;
}
20%, 60% {
    left: 35px;
}
40%, 80% {
    left: 55%;
}
}
@keyframes loginInvalid__logo {
0%, 100% {
    left: 45px;
}
20%, 60% {
    left: 35px;
}
40%, 80% {
    left: 55%;
}
}
@-webkit-keyframes loginInvalid__visor {
0%, 100% {
    left: 55px;
}
20%, 60% {
    left: 45px;
}
40%, 80% {
    left: 65px;
}
}
@keyframes loginInvalid__visor {
0%, 100% {
    left: 55px;
}
20%, 60% {
    left: 45px;
}
40%, 80% {
    left: 65px;
}
}
@-webkit-keyframes loginInvalid__eye {
0%, 100% {
    height: 9px;
    border-top: 0 solid #a7825e;
}
10%, 90% {
    height: 0px;
    border-top: 2px solid #a7825e;
}
}
@keyframes loginInvalid__eye {
0%, 100% {
    height: 9px;
    border-top: 0 solid #a7825e;
}
10%, 90% {
    height: 0px;
    border-top: 2px solid #a7825e;
}
}
.ninja.loginInvalid .ninja__box {
  -webkit-animation: loginInvalid__box 1.5s;
          animation: loginInvalid__box 1.5s;
}
.ninja.loginInvalid .ninja__logo {
  -webkit-animation: loginInvalid__logo 1.5s;
          animation: loginInvalid__logo 1.5s;
}
.ninja.loginInvalid .ninja__visor {
  -webkit-animation: loginInvalid__visor 1.5s;
          animation: loginInvalid__visor 1.5s;
}
.ninja.loginInvalid .ninja__visor > div {
    -webkit-animation: loginInvalid__eye 1.5s;
            animation: loginInvalid__eye 1.5s;
}
@-webkit-keyframes spin {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes spin {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}

.inputs {
