      opacity: 0;
}
.inputs [class*=inputLogin] .field__input input:-webkit-autofill,
    .inputs [class*=inputLogin] .field__input input:-webkit-autofill:hover,
    .inputs [class*=inputLogin] .field__input input:-webkit-autofill:focus
input:-webkit-autofill,
    .inputs [class*=inputLogin] .field__input input {
      border: none;
      height: 35px;
      -webkit-box-shadow: inset 0 0 0 50px #FFF;
