}
.boxButton {
  width: 250px;
  margin: auto;
  text-align: right;
  margin-top: 30px;
}
.boxButton .buttonLogin {
    background: -webkit-gradient(linear, right top, left top, from(#1791c3), to(#00c2ff));
    background: linear-gradient(to left, #1791c3, #00c2ff);
    border: none;
    padding: 8px 25px;
    border-radius: 25px;
    color: #FFF;
    font-size: 18px;
    -webkit-box-shadow: 0px 10px 20px -7px #02bbff;
            box-shadow: 0px 10px 20px -7px #02bbff;
    cursor: pointer;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    outline: none;
}
.boxButton .buttonLogin:hover {
      background-color: #1791c3;
      -webkit-box-shadow: 0px 5px 10px -7px #02bbff;
              box-shadow: 0px 5px 10px -7px #02bbff;
}
.boxButton .buttonLogin[disabled], .boxButton .buttonLogin[disabled]:hover {
      background: #6e849a;
      -webkit-box-shadow: 0 0 0 0 rgba(14, 152, 197, 0.48);
              box-shadow: 0 0 0 0 rgba(14, 152, 197, 0.48);
      cursor: not-allowed;
}
.boxButton .buttonLogin.beforeInit {
      -webkit-transform: translateY(-50px);
              transform: translateY(-50px);
      opacity: 0;
}
